@import '../import/sg.less';
@import '../import/var.less';

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background: #f5f5f5;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

.wrapper {
  .wrapper();
  margin: 0 auto;
}

.header {
  background: white;
  width: 100%;
  height: 10vh;
  max-height: 70px;
  display: flex;
  // box-shadow: inset 0px 4px 8px -3px rgba(17, 17, 17, .06);
  border-bottom: 1px solid rgba(0, 0, 0, .05);
}

.logo-box {
  max-width: 200px;
  display: flex;
  align-items: center;
  margin-left: 15px;
  padding: 15px 0;
}

.menu {
  display: flex;
  height: 90vh;
  max-height: 1080px;
  flex-direction: column;
  background: @bg_menu;

  &_main {
    width: 260px;
  }

  &_rc {
    width: 200px;
  }
}

.content {
  background: #f5f5f5;
  position: relative;

  &_box {
    display: flex;
  }
}

.b-f {
  display: flex;
}

.logo {
  max-width: 260px;
  padding: 32px;
  box-sizing: border-box;
}

.nav {
  max-width: 260px;
  margin: 0;
  padding: 0;
  list-style: none;
  overflow-y: auto;

  &_rc {
    width: 200px;
  }

  &_item {
    width: 100%;
    display: block;
    font-weight: normal;
    font-size: 16px;
    text-transform: uppercase;
    color: @color;
    letter-spacing: 1px;
    border-bottom: 1px solid @bg_content;
    border-top: 1px solid transparent;
    padding: 23px 0 23px 32px;
    box-sizing: border-box;
    cursor: pointer;

    &_rc {
      font-size: 12px;
      padding: 15px 0 15px 33px;
    }

    &:first-child {
      border-top: 1px solid @bg_content;
    }

    &:hover {
      // font-weight: bold;
      color: @bg_menu;
      background: @bg_btn_up;
    }

    &_active {
      // font-weight: bold;
      color: @bg_menu;
      background: @bg_btn_up;
    }
  }
}

.btn_back {
  display: block;
  width: 156px;
  border-radius: 4px;
  border: 2px solid @bg_btn_up;
  background: @bg_menu;
  color: @bg_btn_up;
  line-height: 55px;
  text-align: center;
  font-size: 11px;
  font-weight: bold;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  cursor: pointer;
  text-decoration: none;
  box-sizing: border-box;
  transition: color .2s, background .2s;

  &:hover {
    color: @bg_menu;
    background: @bg_btn_up;
  }

  &_block {
    height: 80px;
    margin: auto auto 40px auto;
    padding-top: 25px;
  }
}

.arrow {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-left: 2px solid @color_hover;
  border-top: 2px solid @color_hover;
}

.left {
  transform: rotateZ(-45deg);
}

.right {
  transform: rotateZ(135deg);
}

.box {
  width: 100%;
  margin: 25px auto;
}

.img {
  display: block;
  // height: 90vh;
  max-height: 90vh;

  &_main {
    height: 90vh;
    margin: 0 auto;
  }
}

.slider {
  //min-width: 600px;
}

.bx-controls {
  position: absolute;
  bottom: 20px;
  left: 50%;
}

.bx-controls-direction {
  position: relative;
}

.bx-prev,
.bx-next {
  width: 50px;
  height: 60px;
  background: #fff;
  text-align: center;
  line-height: 60px;
  cursor: pointer;
}

.bx-prev {
  position: absolute;
  left: 0;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
}

.bx-next {
  position: absolute;
  right: 0;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.bx-pager {
  display: flex;
  padding: 0 25px;
  height: 60px;
  background: #fff;
  margin-left: 52px;
  margin-right: 52px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: normal;
  width: 150px;
  box-sizing: border-box;
}

.bx-pager-item {
  .bx-pager-link {
    display: inline-block;
    width: 34px;
    height: 34px;
    color: inherit;
    border-radius: 50%;
    line-height: 34px;
    text-decoration: inherit;
    text-align: center;
    font-size: 15px;

    &:hover {
      background: @crubs_hover;
      font-weight: bold;
      color: @color_hover;
    }
  }

  a {
    color: inherit;
    text-decoration: inherit;
  }
}

.bx-wrapper {
  transform: scale(1) translateX(-1px);
}

.active {
  background: @crubs_hover;
  font-weight: bold;
  color: @color_hover;
}

.offset {
  &_m_t {
    &_84 {
      margin-top: 84px;
    }
  }
}

.block_center {
  margin: auto;
  display: flex;
}

.km_fixed {
  width: 247px;
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: 350px;
  margin-bottom: 30px;
}

.menu_btn {
  display: none;
  background: white;
  padding: 10px 11px;
  //position: fixed;
  //top: 15px;
  //left: 15px;
  cursor: pointer;
  outline: none;
  border-radius: 3px;
  margin-left: 4px;
  //box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .1), 0 4px 5px 0 rgba(0, 0, 0, .05), 0 1px 10px 0 rgba(0, 0, 0, .05);
}

.buter {
  opacity: 0.5;
}

.btn-phone {
  display: flex;
  align-items: center;
  line-height: 1;
  text-transform: uppercase;
  font-size: 14px;
  padding: 0 15px;
  background: @bg_btn_up;
  color: white;
  cursor: pointer;

  &:hover {
    background: @bg_btn_hover_phone
  }

  &_text {
    margin-left: 10px;
    font-weight: bold;
  }
}

.phone {
  font-size: 18px;
}

.call-phone {
  align-self: center;
  margin-left: auto;
  margin-right: 15px;

  &:hover {
    color: @bg_btn_up;
  }
}

.modal-form {
  position: fixed;
  display: none;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .35);
  justify-content: center;
  &_cont {
    width: 100%;
    margin: 0 auto;
  }
  &_box {
    align-self: center;
    background: white;
    //max-width: 200px;
    padding: 25px 45px;
    border-radius: 5px;
  }
  &_row {
    width: 100%;
    margin-top: 10px;
  }
  &_input {
    display: block;
    width: 100%;
    font-size: 14px;
    color: #333;
    background: #fff;
    border: 1px solid @bg_btn_up;
    font-size: 18px;
    text-align: center;
    padding: 15px 10px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  &_checkbox {
    margin: 0;
  }
  &_text {
    font-size: 10px;
    margin-left: 8px;
  }
  &_agree {
    display: flex;
    align-items: center;
  }
  &_submit {
    width: 100%;
    background: @bg_btn_up;
    outline: none;
    border: 0;
    color: white;
    padding: 15px 10px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    font-size: 18px;
    border: 1px solid transparent;
  }
  &_title {
    text-align: center;
    font-weight: bold;
    //margin: 0;
  }
  &_close-block {
    margin-top: -19px;
    margin-right: -34px;
    text-align: right;
  }
  &_close {
    cursor: pointer;
  }
}
.btn-submit {
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  .logo-box {
    max-width: 100px;
  }

  .img {
    &_main {
      height: auto;
      max-height: 90vh;
      align-self: center;
    }

    margin: 0 auto;
    max-height: 90vh;
  }

  .menu {
    position: absolute;
    z-index: 999;
    top: 0;
    left: -100%;
    display: none;
    height: 100vh;
  }

  .b-f {
    height: 90vh;
  }

  .nav_item {
    font-size: 14px;
    padding: 23px;
  }

  .bx-pager {
    display: none;
  }

  .bx-viewport {
    max-height: 90vh;
  }

  .menu_btn {
    display: flex;
    align-self: center;
  }

  .bg_active_menu {
    position: fixed;
    display: none;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .35);
  }

  .btn_back_block {
    height: 20px;
    margin: auto auto 40px auto;
    padding-top: 20px;
  }

  .btn_back {
    width: 118px;
    line-height: 34px;
    font-size: 8px;
  }

  .logo {
    padding: 8px 50px;
  }

  .btn_back_block {
    margin: auto auto 29px auto;
    padding-top: 18px;
  }

  .nav_item {
    font-size: 11px;
    padding: 16px;
  }

  .offset_m_t_84 {
    margin-top: 50px;
  }

  .bx-controls {
    position: static;
  }

  .bx-controls-direction {
    position: static;
  }

  .bx-prev,
  .bx-next {
    top: 50%;
    margin-top: -15px;
    width: 25px;
    height: 30px;
    line-height: 1;
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .1), 0 4px 5px 0 rgba(0, 0, 0, .05), 0 1px 10px 0 rgba(0, 0, 0, .05);
  }

  .arrow {
    margin-top: 10px;
  }

  .right {
    margin-left: -5px;
  }

  .left {
    margin-right: -5px;
  }

  .phone {
    font-size: 18px;
  }

  .btn-phone {
    background: inherit;
    color: @bg_btn_up;

    &_text {
      display: none;
    }
  }

  .call-phone {
    margin-right: 8px;
    font-size: 8px;
    font-weight: bold;
  }
}